      },
    }).then((res) => {
      // console.log(res)
      this.data = res.data.data
      this.data.forEach(element => {
        console.log(element)
        
      });





      if(this.data.length==0&&
        !(
          this.$store.state.uid == null ||
          this.$store.state.uid == '' ||
          this.$store.state.uid === undefined
        )){
        alert("今天还没有单词要背哦,先去添加单词吧,添加完一般第二天才可以获取,记得今天先背添加的单词哦")
      }
    })
  },
}
